}

.suggested {
    width: 280px;
    height: 350px;
    border: 1px solid rgb(227,227,228);
    border-radius: 10px;
    padding: 20px 15px;
}
  

/* Styling for Upload Page */
.upload-page {
    margin-left: 30px;
}

.image-upload {
    width: 400px;
    height: 230px;
