    width: 500px;
    font-size: 35px;
    margin-top: -20px;
}

button {
    height: 30px;
    width: 100px;
    float: right;
}


/* Styling for Card Components */
.card {
    border-bottom: 1px solid rgb(227,227,228);
    width: 500px;
    height: 600px;
}

.minicard {
    margin: 10px 0 0 0;
    justify-content: space-between;
}

.microcard {
    margin: 10px 0 0 0;
}

.user-profile {
    width: 50px;
    height: 50px;
    background-color: red;
    border-radius: 25px;
    margin-right: 15px;
}

.social-tag {
    margin: 0 20px 0 5px;
}

